import Card from "./Card";
import styled from "styled-components";

const MediaCard = styled(Card)`
  height: 400px;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 400;
  color: #333;
  display: flex;

  img {
    height: 200px;
    width: 100%;
    border-radius: 5px 5px 0 0;
  }
  .mc-title {
    font-weight: 500;
    padding: 0.5em;
  }
  .mc-location {
    padding: 0 0.5em;
  }
  .mc-date {
    padding: 0 0.5em;
  }
  .mc-tags {
    padding: 0.5em;
  }
  button {
    width: 50%;
    margin: auto;
  }
`;

export default MediaCard;
