import React from "react";
import Button from "./components/Button";
import MediaCard from "./components/MediaCard";
import Tag from "./components/Tag";
import "./CardList.css";

const CardList = props => {
  const list = props.data.map((data, i) => {
    return (
      <MediaCard key={i}>
        <img
          src="https://about.canva.com/wp-content/uploads/sites/3/2015/01/music_poster.png"
          alt="poster"
        />
        <span className="mc-title">{data.title}</span>
        <span className="mc-location">{data.location}</span>
        <span className="mc-date">{data.date}</span>
        <span className="mc-tags">
          {data.tags.map(tag => {
            return <Tag>{tag}</Tag>;
          })}
        </span>
        <Button primary>ìì„¸íˆë³´ê¸°</Button>
      </MediaCard>
    );
  });
  return (
    <div className="cardList-container">
      <div className="cardList-label">
        <h3>{props.label}</h3>
      </div>
      <div className="cardList-list">{list}</div>
    </div>
  );
};

export default CardList;
